.feedPage {
  max-width: 1240px;
  max-height: 912px;
  max-height: 100%;
  margin: 40px auto 0;
}

.page {
  display: flex;
  height: 100%;
  max-height: 812px;
  justify-content: space-between;
  margin-top: 20px;
}


.feedTotal {
  width: 584px;
  display: flex;
  flex-direction: column;
  row-gap: 60px;
}

.ordersTotals {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0px;
  margin: 0px;
  gap: 60px;
}

.ordersStatus {
  display: flex;
  gap: 36px;
  padding: 0px;
  margin: 0px;
  list-style: none;
}


